.wo-preview-card {
	min-height: 200px;
	height: 37.5%;
	max-height: fit-content !important;
	width: 100%;
	background-color: #fafafa;
	border: 1px solid #ccc;
	overflow: hidden;
}
.wo-preview-card#right-actions {
	background-color: #fff;
	height: unset;
	min-height: 150px;
	max-height: 200px;
}
.wo-preview-card>.card-content {
	height: 100%;
	padding: 0px;
}
.wo-preview-card>.card-content>.card-content {
	height: 80%;
	max-height: 80%;
	border-bottom: 1px solid #ccc;
}
.wo-preview-card>.card-content>.action-row {
	height: 20%;
	align-items: center;
	max-height: 20%;
	background-color: #f0f0f0;
}
.wo-preview-card>.card-content>.action-row>.action-button {
	width: 40%;
}
.wo-prev-info-cont>.form-info-item {
}
.wo-prev-date {
	font-weight: 700;
	font-size: 27.5px;
}
.wo-prev-tech {
	color: #212121;
	font-style: italic;
	font-size: 22.5px;
}
.wo-prev-cust {
	color: #212121;
	font-style: italic;
	font-size: 17.5px;
	font-weight: 400;
}
.wo-prev-addy {
	color: #212121;
	font-size: 17.5px;
	font-weight: 300;
}

.wo-descr {
	max-height: 50%;
	color: #272727;
	text-align: left;
	overflow-y: auto;
}

/*Buttons*/
.submit-spiff-wo-button {
	background-color: #444f74;
}
.submit-spiff-wo-button:hover {
	background-color: #333c58;
}
.open-wo-button {
	background-color: #56a775;
}
.open-wo-button:hover {
	background-color: #45855d;
}
.remove-wo-button {
	background-color: #a75656;
}
.remove-wo-button:hover {
	background-color: #854545;
}
.view-summary-wo-button {
	background-color: #9756a7;
}
.view-summary-wo-button:hover {
	background-color: #734180;
}

#right-actions-card-content {
	display: grid;
	grid-template-columns: 70% 30%;
}
#right-actions-card-content>.work-order-summary-cont {
	max-height: 100%;
	height: 100%;
	border-bottom: 0px;
}
#right-actions-card-content>.action-row {
	max-height: 100%;
	height: 100%;
}
#right-actions-card-content>.action-row>.action-button {
	width: 70%;
	font-size: larger;
	height: 40px;
	line-height: 40px;
}