body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.outline-card {
	border: 1px solid #ccc;
	filter: unset;
}

.no-padding {
	padding: 0px;
}

.h2 {
	font-size: 22px;
}

.side-bar-menu-cont>.side-bar-menu-controls>:first-child>img {
	transform: rotate(180deg);
	transition: 0.5s;
}

.side-bar-menu-cont.collapsed>.side-bar-menu-controls>:first-child>img {
	transform: unset;
}

/*Dock*/
.dock {
    height: calc(100vh - 40px);
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
  
.dock-app-button {
    height: 125px;
    width: 125px;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    color:#282c34;
    line-height: 0px;
    font-weight: 600;
    cursor: pointer;
    filter: none;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.dock-app-button>img {
	height: 30%;
	width: 30%;
}
.dock-app-button>div {
	height: 35px;
	line-height: 17.5px;
	width: 80%;
	margin-top: 5px;
}

.dock-app-button:hover {
    background-color: #ececec;
    color:#282c34;
}

.dock-form-card {
	display: grid;
	grid-template-rows: 65% 35%;
	border: 1px solid #f3f3f3;
	border-radius: 10px;
	text-align: center;
}

.dock-form-card>.paragraph-cont {
	padding: 10px;
	width: 100%;
}

.dock-form-card-controls>.action-button {
	width: 100%;
}

.dock-form-card-controls {
	background-color: #f3f3f3;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	padding: 20px;
}

.dock-cont {
}

.one-third-height {
	height: 35%;
	width: 100%;
}


.two-thirds-height {
	height: 65%;
}
.text {
	text-align: left;
	padding: 10px;
	font-size: 20px;
	font-weight: 600;
	color: #282c34;
}

.underline {
	background-color: #282c34;
	width: 99%;
	height: 2px;
}

.app-row-header {
	height: calc(100% - 5px);
	overflow-y: scroll;
	margin-top: 5px;
}

.app-row {
	padding-top: 10px;
	padding-left: 5px;
	padding-right: 5px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	height: 95%;
	overflow-y: scroll;
}

.notifications-cont {
	padding: 10px;
	height: 80%;
	display: flex;
	flex-direction: column;
	gap: 10px;
}

/*HEADERS*/
.table-row.header {
	background-color: transparent;
	margin: 0px;
	padding: 0px;
	border-bottom: 1px solid #ccc;
	background-color: #efefef;
}
.table-row.header:hover {
	background-color: #efefef;
}
.table-row.header>.rowitem {
	background-color: transparent;
	margin: 0px;
	border-right: 1px solid #ccc;
	padding: 10px;
}

/*ROW ITEM*/
.rowitem {
	margin: 0px;
	background-color: transparent;
}
.rowitem.clickable {
	cursor: pointer;
	background-color: aqua;
}
.rowitem.clickable:hover {
	background-color: #ccc;
}

/*TABLEROW*/
.table-row {
	height: fit-content;
	padding: 10px;
	border-bottom: 1px solid #ccc;
	display: grid;
	grid-template-columns: repeat(5, 20%)
}
.table-row.clickable {
	cursor: pointer;
}
.table-row:hover {
	background-color: #fafafa;
}

/*CONTAINER*/
.tablerow-container {
	height: calc(100% - 40px);
	overflow-y: auto;
}

#testingcont {
	background-color: aquamarine;
	height: 100px;
	width: 100px;
}






#testing-card {
	height: 90vh;
}

#testing-card>.card-content {
	height: 100%;
}

/*Vogel Home*/
.vogel-home-container {
	margin-left: 10px;
	margin-top: 10px;
	padding: 10px;
	background-color: #fafafa;
	border-top: 1px solid #ccc;
	border-left: 1px solid #ccc;
	border-top-left-radius: 10px;
	height: calc(100% - 10px);
	width: calc(100% - 10px);
}

.vogel-home-container>.header {
	height: 12.5%;
}
.vogel-home-container>.content {
	height: 87.5%;
}
.vogel-home-container>.footer {
	height: 0%;
}

.vogel-home-container>.header>.header-text>.h1 {
	font-size: 30px;
	text-align: left;
	font-weight: 700;
}
.vogel-home-container>.header>.header-text>#welcome-label {
	text-align: left;
	font-style: italic;
	font-size: small;
}
.vogel-home-container>.header>.header-text>#welcome-label>span {
	font-style: normal;
	font-weight: 550;
	color: #bb1313;
}

.notification-bell {
	width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	justify-self:end;
	position: relative;
	bottom: 50px;
	left: calc(100% - 60px);
	border-radius: 5px;
	cursor: pointer;
}
.notification-bell#active {
	background-color: #3886a53d;
}
.notification-bell#inactive {
	background-color: transparent;
}
.notification-bell#inactive:hover {
	background-color: #ccc;
}
.notification-bell>img {
	height: 30px;
	width: 30px;
}
.notification-card {
	background-color: #fafafa;
	border: 1px solid #ccc;
	border-radius: 10px;
	padding: 10px;
	width: 400px;
	height: 300px;
	z-index: 100;
	display: grid;
	grid-template-columns: 100% 100%;
	filter: var(--drop-shadow);
	overflow: hidden;
	position: absolute;
	top: 70px;
	left: calc(100% - 420px);
}
.notification-card>.action-row {
	width: 800px;
	gap: 40px;
	position: relative;
	left: 0px;
}
.notification-card>.action-row.unread {
	animation-name: showUnread;
	animation-duration: 0.5s;
	left: 0px;
}
.notification-card>.action-row.read {
	left: -420px;
	animation-name: showRead;
	animation-duration: 0.5s;
}

@keyframes showRead {
	from {
	  	left: 0px;
	}
  
	to {
	  	left: -420px;
	}
}

@keyframes showUnread {
	from {
	  	left: -420px;
	}
  
	to {
		left: 0px;
	}
}


.notif-title-row {
	display: grid;
	grid-template-columns: 95% 5%;
	align-items: center;
	justify-content: center;
	gap: 0px;
}
.notif-title {
	font-size: large;
	font-weight: 700;
	text-align: left;
	height: min-contents;
	line-height: 28px;
}
.notification-bell>.new-notif {
	background-color: #bb1313;
	height: 10px;
	width: 10px;
	position:absolute;
	top: 2.5px;
	left: 22.5px;
	border-radius: 10px;
	filter: drop-shadow(1px 1px 1px #d45757);
}
.notifications-container {
	width: 400px;
	height: 100%;
}
.notifications-container.read>.notification-list {
	padding: 5px;
	height: calc(100% - 30px);
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
	gap: 5px;
}
.notifications-container.unread>.notification-list {
	padding: 5px;
	height: calc(100% - 60px);
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.vogel-home-container>.content>.two-pane {
	display: grid;
	grid-template-columns: 40% 60%;
	height: 35%;
}

#recent-apps {
	height: 100%;
	overflow: hidden;
	padding: 10px;
}
#recent-apps>.h2 {
	text-align: left;
	font-weight: 650;
	color: #323232;
	height: 30px;
	font-style: italic;
}
#recent-apps>.app-list {
	display: flex;
	flex-direction: row;
	gap: 10px;
	height: calc(100% - 30px);
	overflow-y: scroll;
}
#recent-apps>.app-list>.app-link {
	background-color: #efefef;
	height: 85px;
	width: 85px;
	color: #171717;
	border-radius: 10px;
	border: 1px solid #ccc;
	align-items: center;
	justify-content: center;
}
#recent-apps>.app-list>.app-link:hover {
	background-color: #ccc;
}


.home-header {
	text-align: left;
	font-size: 30px;
}

#widget-container {
	background-color: #efefef;
	border-radius: 10px;
	border: 1px solid #ccc;
	height: 65%;
	width: 100%;
}

/*Portal Sidebar*/
.dock-cont {
	height: 100%;
	display: grid;
	grid-template-columns: 75px calc(100% - 75px);
}
.sidebar-logo {
	padding-left: 20px;
	padding-right: 20px;
	padding-top: 15px;
}
.side-bar-app-select {
	filter: drop-shadow(2.5px 0px 2px #999999);;
	height: 100%;
	width: 75px;
	background-color: #062a49;
	position: relative;
	z-index: 100;
}

.side-bar-app-select>.app-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.side-bar-app-select>.static-actions {
	position: absolute;
	filter: unset;
	bottom: 0px;
	background-color: #515f7a;
	width: 75px;
	height: fit-content;
	display: flex;
	flex-direction: column;
}


.app-icon-container {
	height: 40px;
	position: relative;
	z-index: 200;
}


.app-icon {
	color: #fff;
	background-color: #062a49;
	border-radius: 0px;
	filter: unset;
	font-weight: 600;
	font-size: 20px;
	height: 50px;
	width: 100%;
	cursor: pointer;
	position: relative;
	z-index: 200;
}
.app-icon.image>img {
	height: 30px;
	width: 30px;
}

.app-icon.image {
	display: flex;
	align-items: center;
	justify-content: center;
}

.app-icon-list {
	background-color: #062a49;
	left: -300px;
	bottom: 50px;
	width: fit-content;
	width: 300px;
	position: relative;
	z-index: 0;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: hidden;
}

.app-icon-list#true {
	left: 75px;
}
.app-icon:hover {
	background-color: #515f7a;
}

.app-link {
	background-color: #062a49;
	filter: unset;
	display: flex;
	color: #fff;
	padding: 10px;
	cursor: pointer;
	position: relative;
	z-index: 0;
	font-weight: 600;
	gap: 10px;
	height: 50px;
	border-bottom-left-radius: 0px;
	border-top-left-radius: 0px;
	width: 100%;
}
.app-link:hover {
	background-color: #515f7a;
}



/*App Dock*/
#app-dock-card {
	max-width: unset;
	width: 90vw;
	min-height: unset;
	max-height: 60vh;
	height: fit-content;
}
.app-dock-list-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 10px;
	row-gap: 10px;
	padding: 10px;
}
.app-dock-button {
	height: 150px;
	width: 150px;
	background-color: #efefef;
	filter: unset;
	border: 1px solid #ccc;
	color: #202020;
	font-size: medium;
	font-weight: 600;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.app-dock-button:hover {
	background-color: #ccc;
	color: #000;
}
.app-dock-row-header {
	text-align: left;
	font-size: xx-large;
	font-weight: 700;
}