.App {
  text-align: center;
}

.text-only-button {
	width: max-content;
	background-color: transparent;
	filter: unset;
	color: #c94a4c;
	cursor: pointer;
	font-weight: 650;
}
.text-only-button:hover {
	background-color: transparent;
	color: #912d2f;
}

/*NEW LOGIN*/
.login-container {
	background-color: #fafafa;
	width: 100vw;
	height: 100vh;
	display: grid;
	grid-template-columns: 30% 70%;
	font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.login-container.dark {
	background-color: #202020;
}
.logo-header {
	display: flex;
	align-items: end;
	gap: 10px;
}
.logo-header-text {
	font-size: 25px;
	font-weight: 600;
}
.login-top-logo {
	height: 40px;
	width: 40px;
}

.login-form-container {
	height: 100%;
	width: 90%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: start;
	gap: 10px;
	margin-top: 10%;
	margin-left: 5%;
}
.login-form {
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	justify-content: center;
	gap: 10px;
}
.login-form>.form-info-item {
	width: 100%;
}

.forgot-password-cont {
	width: 90%;
	padding: 10px;
}

.login-splash {
	background-image: url('/home/image.jpg');
	background-position: 50%;
	background-size:cover;
}

.login-splash-notes {
	background-color: rgba(32, 32, 32, 0.25);
	border: 1px solid rgba(32, 32, 32, 0.375);
	display: flex;
	flex-direction: row-reverse;
	width: fit-content;
	padding: 10px;
	gap: 5px;
	border-radius: 5px;
	margin-top: 10px;
	margin-left: 10px;
	align-items: center;
	justify-content: center;
}
.login-splash-notes>img {
	height: 15px;
	width: 15px;
}
.login-splash-notes>div {
	color: #fff;
	font-size: 12px;
	font-weight: bold;
}

.login-submit-button {
	height: 50px;
	font-size: 35px;
	line-height: 45px;
	background-color: #c9724a;
}
.login-submit-button:hover {
	background-color: #a75e3c;
}

.form-item-top-text {
	display: flex;
	flex-direction: column;
}
.form-item-top-text>.form-info-title {
	font-size: 15px;
	font-weight: 600;
	color: #404040;
	top: 15px;
	left: 20px;
	z-index: 100;
	padding-left: 6px;
	padding-right: 6px;
}
.form-item-top-text>input {
	width: 100%;
	height: 50px;
	font-size: larger;
	font-weight: 250;
	border-bottom: 2px solid #ccc;
}
.form-item-top-text>input:focus {
	border-bottom: 2px solid rgb(59, 131, 190);
	transition: 0.15s;
}

#hide {
	display: none;
}

/*Activity center*/
.activity-card {
	min-height: unset;
	height: 100%;
	max-width: unset;
	width: 99%;
}

#activity-card-card-content {
	height: 100%;
}

#activity-card-card-content>.content {
	display: grid;
	grid-template-columns: 60% 39%;
	gap: 1%;
	height: 100%;
}

.tech-dash {
	height: 90%;
	overflow-y: auto;
	background-color: #fafafa;
	border-radius: 10px;
	border: 1px solid #ccc;
	padding: 0px;
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.tech-dash.trackers {
	padding: 0px;
}

.tech-dash.trackers>.month-label {
	padding: 10px;
	font-size: 30px;
	display: none;
	height: 30px;
}

.tech-dash.trackers>.table-row.header {
	background-color: #062a49;
}

.tech-dash.trackers>.table-row.header>.rowitem {
	border-right: 0px;
	color: #fff;
	font-weight: 700;
}

.trackers-chart-dash {
	padding: 10px;
	height: 100%;
}
.trackers-widget {
	padding: 0px;
	height: calc(100%);
	overflow-y: hidden;
}
.trackers-widget>.tablerow-container {
	overflow-y: scroll;
}

.work-order-dash {
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding: 10px;
}

.month-label {
	padding: 10px;
	font-size: 25px;
	background-color: #062a49;
	color: #fff;
}

#one {
	background-color: #912d2f;
	color: #fff;
}

#two {
	background-color: #a75e3c;
	color: #fff;
}

#three {
	background-color: #c94a4c;
	color: #fff;
}

#four {
	background-color: #062a49;
	color: #fff;
}
.gallery-item {
	height: 100%;
	width: 100%;
}

.gallery-container {
	height: 100%;
	width: 100%;
	overflow-x: hidden;
}

.gallery-item.right {
	margin-left: 300%;
	width: 300%;
	display: none;
}

.gallery-item.center {
	/*animation-name: slidein;
	animation-duration: 3s;*/
	filter: unset;
}
.gallery-item.center {
	
}

.gallery-item.left {
	animation-name: slideout;
	animation-duration: 3s;
}

@keyframes slidein {
	from {
	  	margin-left: 300%;
		width: 300%;
	}
  
	to {
	  	margin-left: 0%;
		width: 100%;
	}
}

@keyframes slideout {
	from {
	  	margin-left: 0%;
		width: 100%;
	}
  
	to {
		margin-left: -300%;
		width: 300%;
	}
}